import React, {useState, useEffect} from 'react';
import { InputField } from '../UI/InputField/InputField.jsx';
import classes from '../../styles/cabinetParts.module.css';

export const Delivery = () => {
    const [City, setCity] = useState('');
    const [PostNum, setPostNum] = useState('');
    const [Payment, setPayment] = useState('');

    useEffect(() => {
        //      
        const savedCity = localStorage.getItem('City');
        const savedPostNum = localStorage.getItem('Post');
        const savedPayment = localStorage.getItem('Payment');

        if (savedCity) setCity(savedCity);
        if (savedPostNum) setPostNum(savedPostNum);
        if (savedPayment) setPayment(savedPayment);
    }, []);

    const handleSubmit = (e) => {
        e.preventDefault();

        //    localStorage
        localStorage.setItem('City', City);
        localStorage.setItem('PostNum', PostNum);
        localStorage.setItem('Payment', Payment);

        //    
        setCity('');
        setPostNum('');
        setPayment('');
    };

    return (
        <form className={classes.form} onSubmit={handleSubmit}>
            <div className={classes.input__div}>
                <InputField text=":" type="text" id="City" 
                    value={City} 
                    onChange={(e) => setCity(e.target.value)}
                />
            </div>
            <div className={classes.input__div}>
                <InputField text=" :" type="text" id="PostNum" placeholder='00'
                    value={PostNum} 
                    onChange={(e) => setPostNum(e.target.value)}
                />
            </div>
            <div className={classes.input__div}>
                <InputField text=" :" type="text" id="Payment" placeholder=' / '
                    value={Payment} 
                    onChange={(e) => setPayment(e.target.value)}
                />
            </div>
            <button className={classes.btn}></button>
        </form>
    )
}